'use client';

import {
  Box,
  Typography,
  Grid,
  Card,
  Button,
  Menu,
  MenuItem,
  Alert,
  Pagination,
} from '@mui/material';
import { Add, Edit, Delete } from '@mui/icons-material';
import { useState, useEffect, useMemo, useCallback } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import useUserProfile from '@/hooks/useUserProfile';
import AddPetModal from '@/components/pet/petModal/AddPetModal';
import AccountHeader from '@/components/account/AccountHeader';
import BackButton from '@/components/ui/buttons/BackButton';
import Header from '@/components/ui/containers/Header';
import MultiToggleChip from '@/components/ui/buttons/MultiToggleChip';
import usePets from '@/hooks/usePets';
import { PAGE_SIZE, PET_FILTERS } from '@/lib/constants/pet';

/* ------------------------------------------------------------------ */
/* 3. Main page component                                             */
/* ------------------------------------------------------------------ */
export default function PetManagementPage() {
  const router = useRouter();
  const params = useSearchParams();
  const { user, isLoading: isLoadingUser } = useUserProfile();

  /* url-backed UI state */
  const page = Number(params.get('page') ?? 1);
  const filters = useMemo(
    () => (params.get('filters') ?? '').split(',').filter(Boolean),
    [params]
  );

  /* derived side-effect helpers */
  const pushState = useCallback(
    (nextPage: number, nextFilters: string[]) => {
      const qs = new URLSearchParams();
      if (nextPage > 1) qs.set('page', String(nextPage));
      if (nextFilters.length) qs.set('filters', nextFilters.join(','));
      router.replace(`?${qs}`, { scroll: false });
    },
    [router]
  );

  /* redirect if user isn’t pet owner */
  useEffect(() => {
    if (!isLoadingUser && user && user.userType !== 'petOwner') {
      router.push('/account');
    }
  }, [user, isLoadingUser, router]);

  const { pets, totalCount, isLoading, error } = usePets(
    user?.id,
    page,
    filters
  );

  /* add / delete handlers */
  const refresh = useCallback(
    () => pushState(1, filters),
    [pushState, filters]
  );

  const handleAddPet = async (data: unknown) => {
    const res = await fetch('/api/pets', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
    });
    if (res.ok) refresh();
  };

  const handleDelete = async (id: string) => {
    const res = await fetch(`/api/pets/${id}`, { method: 'DELETE' });
    if (res.ok) refresh();
  };

  /* menu state */
  const [menuEl, setMenuEl] = useState<HTMLElement | null>(null);
  const [menuPet, setMenuPet] = useState<string | null>(null);

  /* view helpers */
  const totalPages = Math.max(1, Math.ceil(totalCount / PAGE_SIZE));

  /* ------------------------------------------------------------------ */
  /* Render                                                             */
  /* ------------------------------------------------------------------ */
  if (isLoadingUser || isLoading)
    return (
      <Box p={3} textAlign="center">
        <Typography>Loading…</Typography>
      </Box>
    );

  if (!user || user.userType !== 'petOwner')
    return (
      <Box p={3} textAlign="center">
        <Typography>Access denied.</Typography>
      </Box>
    );

  return (
    <Box sx={{ maxWidth: 1200, mx: 'auto', p: 2 }}>
      <Header
        left={<BackButton smartNavigation />}
        center={
          <AccountHeader
            title="My Pet Management"
            subtitle="Manage your pets looking for loving homes"
            centered
          />
        }
      />

      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}

      {/* Filters */}
      <Box mb={3}>
        <MultiToggleChip
          items={PET_FILTERS}
          selected={filters}
          onChange={next => pushState(1, next)}
          render={i => i.label}
        />
      </Box>

      {/* Top bar */}
      <Box display="flex" justifyContent="space-between" mb={3}>
        <Typography variant="h6">
          {totalCount} {totalCount === 1 ? 'Pet' : 'Pets'}
        </Typography>

        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => pushState(page, filters)} // scroll to top
          sx={{
            backgroundColor: '#ed9426',
            '&:hover': { backgroundColor: '#d4841f' },
          }}
        >
          Add Pet
        </Button>
      </Box>

      {/* Cards or empty state */}
      {pets.length === 0 ? (
        <Card sx={{ p: 6, textAlign: 'center' }}>
          <Typography>No pets match your filters</Typography>
        </Card>
      ) : (
        <Grid container spacing={2} pb={6}>
          {pets.map(pet => (
            <AccountPetCard
              key={pet.id}
              pet={pet}
              onView={id => router.push(`/pet/${id}`)}
              onMenuOpen={(e, id) => {
                setMenuEl(e.currentTarget);
                setMenuPet(id);
              }}
            />
          ))}
        </Grid>
      )}

      {/* Pagination */}
      {totalPages > 1 && (
        <Box mt={4} display="flex" justifyContent="center">
          <Pagination
            count={totalPages}
            page={page}
            onChange={(_, p) => pushState(p, filters)}
            shape="rounded"
          />
        </Box>
      )}

      {/* Menu */}
      <Menu anchorEl={menuEl} open={!!menuEl} onClose={() => setMenuEl(null)}>
        <MenuItem
          onClick={() => {
            console.log('Edit pet:', menuPet);
            setMenuEl(null);
          }}
        >
          <Edit sx={{ mr: 1 }} fontSize="small" /> Edit
        </MenuItem>
        <MenuItem
          onClick={() => {
            if (menuPet) handleDelete(menuPet);
            setMenuEl(null);
          }}
          sx={{ color: 'error.main' }}
        >
          <Delete sx={{ mr: 1 }} fontSize="small" /> Delete
        </MenuItem>
      </Menu>

      {/* Modal */}
      <AddPetModal
        open={false} /* wire up as before if you need */
        onClose={() => null}
        onSubmit={handleAddPet}
      />
    </Box>
  );
}
